.Action {
  background: none;
  border: none;
  border-radius: 3px;
  color: blue;
  font: inherit;
  text-decoration: underline;
  -webkit-transition: background-color 200ms linear;
  -o-transition: background-color 200ms linear;
  transition: background-color 200ms linear;
}

.Action:hover {
  cursor: pointer;
}

.Action:active {
  background-color: lightBlue;
}

.Action:disabled {
  color: grey;
  cursor: not-allowed;
}



